import React from "react";
import "./App.css";
import CatFacts from "./CatFacts";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { fact: "" };
  }
  getFact = () => {
    fetch("https://catfact.ninja/fact?max_length=140")
      .then((response) => response.json())
      .then((data) => {
        this.setState({ fact: data.fact });
      });
  };
  render() {
    return (
      <div className="App">
        <CatFacts fact={this.state.fact} />
        

        <div class="button_cont" onClick={this.getFact} align="center">
          <p className='button'>New Fact!</p>
         
        </div>
      </div>
    );
  }
}

export default App;
