import React from "react";
import "./CatFacts.css";

const CatFacts = ({ fact }) => {
  if (fact === "") {
    return (
      <div className="container">
        <div>
          {" "}
          <h1>Press the button below for a cat fact!</h1>
        </div>
      </div>
    );
  } else {
    return (
      <div className="container">
        <div>
          <h1>{fact}</h1>
        </div>
      </div>
    );
  }
};

export default CatFacts;
